import React, {FC, Fragment, useEffect, useState} from "react";
import './styles/styles.scss';

// Redux
import { CartItemProps } from "../Cart/components/CartBlock/CartItem/CartItem";
import {priceFormater} from "../../lib/priceFormater";
import { Link } from "gatsby";
import {useDispatch, useSelector} from "react-redux";
import {RootState} from "../../store/store";
import {addToCart} from "../../store/slice/cart";

export type CatalogItemProps = {
    ID: string,
    IMAGE: string;
    IMAGES: string[];
    HOVER_IMAGE?: string;
    NAME: string;
    SUBTITLE?: string;
    DESCRIPTION?: string;
    TONE?: string;
    WEIGHT?: number;
    PRICE: number;
};

export const CatalogItem: FC<CatalogItemProps> = ({
  IMAGE,
  NAME,
                                                      HOVER_IMAGE,
  DESCRIPTION,
  TONE,
  WEIGHT,
                                                      SUBTITLE,
                                                      PRICE,
                                                      ID
}) => {
    const [inCart, setInCart] = useState<boolean | undefined>(null);
    const dispatch = useDispatch();
    const {items} = useSelector((state: RootState) => state.cart);

    const onClickHandler = () => {
        const payload: CartItemProps = {
            ID,
            IMAGE,
            NAME,
            PRICE,
            COUNT: 1
        }

        dispatch(addToCart(payload));
    };

    useEffect(() => {
        if (items.find((item) => item.ID === ID)) {
            setInCart(false)
        } else {
            setInCart(true)
        }
    }, [items])

    return (
        <article className="catalogItem">
            <div className="catalogItem__Content">
                <Link to={`/product/${ID}`}>
                    <div className="catalogItem__ImageWrap">
                        <img decoding="async" loading="lazy" src={IMAGE}/>
                        <img decoding="async" loading="lazy" src={HOVER_IMAGE ? HOVER_IMAGE : ``} className="hoverImage"/>
                        <div className="blurEffect" />
                    </div>
                </Link>

                <h4 className="catalogItem__Title">
                    {NAME}
                </h4>
                <div className="catalogItem__Description">
                    <span>{SUBTITLE}</span>
                </div>
                <div className="catalogItem__Info">
                    <span>{TONE}</span>
                    {
                        WEIGHT && "|"
                    }
                    <span>{WEIGHT}</span>
                </div>
                <div className="catalogItem__BottomPanel">
                    <div className="catalogItem__Price">
                        <span>
                            {priceFormater.format(PRICE) + ""}
                        </span>
                    </div>
                    <BuyButton inCart={inCart} onClickHandler={onClickHandler}/>
                </div>
            </div>
        </article>
    );
}

export type BuyButtonProps = {
    big?: boolean;
    onClickHandler?: () => void;
    inCart: boolean;
};

export const BuyButton:FC<BuyButtonProps> = ({
                                                 big,
                                                 onClickHandler,
                                                 inCart
                                             }) => {

    return (
        <Fragment>
            {
                inCart
                    ? <button className={`catalogItem__Button ${big ? `big` : ``}`}
                              onClick={onClickHandler}
                    >
                        <span>
                            
                        </span>
                    </button>
                    :
                    <span className={`inCart catalogItem__InCartText ${big ? `big` : ``}`}>
                         
                    </span>
            }
        </Fragment>

    );
}