export type FooterProps = {};

export const Footer: FC<FooterProps> = ({}) => (
    <footer className="footer">
        <div className="footer__Content">
            <Row className="footerRow" gutter={[20, 20]} >
                <Col xs={12} sm={8} lg={4} xl={3}>
                    <h4 className="footer__Title">
                         
                    </h4>
                    <FooterPageList items={ListItems1}/>
                </Col>
                <Col xs={12} sm={8} lg={4} xl={3}>
                  <h4 className="footer__Title">
                     
                  </h4>
                  <FooterPageList items={ListItems2}/>
                </Col>
                <Col xs={12} sm={8} lg={4} xl={3}>
                  <h4 className="footer__Title">
                     
                  </h4>
                  <FooterPageList items={ListItems3}/>
                </Col>
                <Col xs={0} xl={5}></Col>
                <Col xs={24} lg={6} xl={5}>
                    <h4 className="footer__Title">
                        
                    </h4>
                    <div className="footer__BlockValue">
                        <span>. , . ,</span>
                        <span>.  , 50</span>
                        <span>420127</span>
                    </div>
                </Col>
                <Col xs={24} lg={6} xl={5}>
                    <h4 className="footer__Title">
                          
                    </h4>
                    <FooterSiteInfo />
                </Col>
            </Row>
        </div>
        <BottomFooterPanel />
    </footer>
);

export type FooterPageListProps = {
  items: {
    NAME: string;
    LINK: string;
  }[]
}

export const FooterPageList: FC<FooterPageListProps> = ({items}) => (
    <div className="footerPageList">
      {
        items.map(({NAME, LINK}) => (
          <a className="footerPageList__Item" href={LINK}>
            {NAME}
          </a>
        ))
      }
    </div>
);

export const FooterSiteInfo = ({}) => (
    <div className="footerSiteInfo">
        <div className="footerSiteInfo__PhoneNumber">
          <Tooltip placement="top" title=" " color={mainTheme.colorsList.greenColor1}>
            <a href="tel:+795133412333">
                +7 951 334 123 33
            </a>
          </Tooltip>
        </div>
        <div className="footerSiteInfo__Mail">
            <span>
                drumfado@yandex.ru
            </span>
        </div>
        <div className="footerSiteInfo__SocialList">
            <Tooltip placement="top" title=" Instagram" color={mainTheme.colorsList.greenColor1}>
              <a className="footerSiteInfo__SocialLink">
                  <InstagramIcon width="24" height="24"/>
              </a>
            </Tooltip>
            <Tooltip placement="top" title=" Youtube" color={mainTheme.colorsList.greenColor1}>
              <a className="footerSiteInfo__SocialLink">
                <YoutubeIcon width="24" height="24"/>
              </a>
            </Tooltip>
          <Tooltip placement="top" title=" WhatsApp" color={mainTheme.colorsList.greenColor1}>
            <a className="footerSiteInfo__SocialLink">
              <WhatsAppIcon width="24" height="24"/>
            </a>
          </Tooltip>
        </div>
    </div>
);

import PayImage from "/src/images/payList.png";

export type BottomFooterPanelProps = {
  // items: ReactElement[];
}

export const BottomFooterPanel = ({}) => (
  <div className="bottomFooterPanel">
    <div className="bottomFooterPanel__List">
      <img src={PayImage} height={20} width={288}/>
    </div>
  </div>
);
