import React, {FC} from "react";
import './styles/styles.scss';

import {Link} from "gatsby";
import {idGenerator} from "../../../../lib/idGenerator";

export type PageListProps = {
    items: PageLinkProps[];
};

type PageLinkProps = {
    title: string;
    href: string;
}

export const PageList:FC<PageListProps> = ({items}) => {

    return (
        <div className="pageList">
            {
                items.map(item => (
                    <Link to={`${item.href}`} className="pageLink">
                            <span>
                                {item.title}
                            </span>
                    </Link>

                ))
            }
        </div>
    );
}
